/**
 * ãƒ«ãƒ¼ã‚¿ãƒ¼
 * ãƒãƒƒã‚·ãƒ¥ãƒ™ãƒ¼ã‚¹ã®SPAãƒ«ãƒ¼ãƒ†ã‚£ãƒ³ã‚°
 */

import { MenuView } from '../ui/views/MenuView';
import { VerticalKeyView } from '../ui/views/VerticalKeyView';
import { HorizontalKeyView } from '../ui/views/HorizontalKeyView';
import { FlashcardView } from '../ui/views/FlashcardView';
import { KochView } from '../ui/views/KochView';
import { ListeningView } from '../ui/views/ListeningView';

/**
 * ãƒ“ãƒ¥ãƒ¼ã‚¤ãƒ³ã‚¿ãƒ¼ãƒ•ã‚§ãƒ¼ã‚¹
 */
export interface View {
	/**
	 * ãƒ“ãƒ¥ãƒ¼ã‚’ãƒ¬ãƒ³ãƒ€ãƒªãƒ³ã‚°ã™ã‚‹
	 */
	render(): void | Promise<void>;

	/**
	 * ãƒ“ãƒ¥ãƒ¼ã‚’ç ´æ£„ã™ã‚‹
	 */
	destroy(): void;
}

/**
 * ãƒ«ãƒ¼ãƒˆå®šç¾©
 */
type Route = {
	path: string;
	view: new () => View;
};

/**
 * ãƒ«ãƒ¼ã‚¿ãƒ¼ã‚¯ãƒ©ã‚¹
 */
export class Router {
	private currentView: View | null = null;
	private routes: Route[] = [];

	constructor() {
		//! ãƒ«ãƒ¼ãƒˆå®šç¾©ã€‚
		this.routes = [
			{ path: '', view: MenuView },
			{ path: 'menu', view: MenuView },
			{ path: 'vertical', view: VerticalKeyView },
			{ path: 'horizontal', view: HorizontalKeyView },
			{ path: 'flashcard', view: FlashcardView },
			{ path: 'koch', view: KochView },
			{ path: 'listening', view: ListeningView },
		];
	}

	/**
	 * ãƒ«ãƒ¼ã‚¿ãƒ¼ã‚’åˆæœŸåŒ–ã™ã‚‹
	 */
	init(): void {
		//! ãƒãƒƒã‚·ãƒ¥å¤‰æ›´æ™‚ã®ãƒªã‚¹ãƒŠãƒ¼ã‚’ç™»éŒ²ã€‚
		window.addEventListener('hashchange', () => this.handleRoute());

		//! åˆå›ãƒ«ãƒ¼ãƒ†ã‚£ãƒ³ã‚°ã€‚
		this.handleRoute();
	}

	/**
	 * ç¾åœ¨ã®ãƒãƒƒã‚·ãƒ¥ã«åŸºã¥ã„ã¦ãƒ«ãƒ¼ãƒ†ã‚£ãƒ³ã‚°ã™ã‚‹
	 */
	private handleRoute(): void {
		//! ç¾åœ¨ã®ãƒ“ãƒ¥ãƒ¼ã‚’ç ´æ£„ã€‚
		if (this.currentView) {
			this.currentView.destroy();
			this.currentView = null;
		}

		//! ãƒãƒƒã‚·ãƒ¥ã‹ã‚‰ãƒ‘ã‚¹ã‚’å–å¾—ï¼ˆ#ã‚’é™¤å»ï¼‰ã€‚
		const hash = window.location.hash.slice(1);
		const route = this.routes.find(r => r.path === hash);

		if (route) {
			//! ãƒ“ãƒ¥ãƒ¼ã‚’ä½œæˆã—ã¦ãƒ¬ãƒ³ãƒ€ãƒªãƒ³ã‚°ã€‚
			this.currentView = new route.view();
			this.currentView.render();
		} else {
			//! è©²å½“ã™ã‚‹ãƒ«ãƒ¼ãƒˆãŒãªã„å ´åˆã¯ãƒ¡ãƒ‹ãƒ¥ãƒ¼ã«é·ç§»ã€‚
			window.location.hash = '#menu';
		}
	}

	/**
	 * æŒ‡å®šã—ãŸãƒ‘ã‚¹ã«é·ç§»ã™ã‚‹
	 */
	navigate(path: string): void {
		window.location.hash = `#${path}`;
	}
}
